import React from "react";
import ReactApexChart from "react-apexcharts";

class EntryExitPointChart extends React.Component {
  constructor(props) {
    super(props);
    console.log("PROPS", props.data);
    const graphData = props.data;
    const seriesData =graphData.length>0? graphData[0].series!=null&&graphData[0].series!=undefined
    ? graphData[0].series
    : [
        { name: "entry", data: [] },
        { name: "exit", data: [] },
      ]: [
        { name: "entry", data: [] },
        { name: "exit", data: [] },
      ];

  const timeData =graphData.length>0? graphData[0].time ? graphData[0].time : []:[];

    this.state = {
      // series: [
      //   {
      //     name: "Entry",
      //     data: [500, 230, 600, 360, 700, 890, 750, 420, 600, 300, 420, 220],
      //   },
      //   {
      //     name: "Exit",
      //     data: [250, 380, 200, 300, 200, 520, 380, 770, 250, 520, 300, 900],
      //   },
      // ],
      series: seriesData,
      options: {
        chart: {
          height: 350,
          type: "area",
          group: "social",
          toolbar: {
            show: false,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          width: [2, 2],
          colors: ["#f8857d", "#709fba"],
          curve: "straight",
        },
        legend: {
          tooltipHoverFormatter: function (val, opts) {
            return (
              val +
              " - " +
              opts.w.globals.series[opts.seriesIndex][opts.dataPointIndex] +
              ""
            );
          },
          markers: {
            fillColors: ["#f8857d", "#709fba"],
            width: 19,
            height: 19,
            strokeWidth: 0,
            radius: 19,
          },
        },
        markers: {
          size: 6,
          border: 0,
          colors: ["#f8857d", "#709fba"],
          hover: {
            size: 6,
          },
        },
        xaxis: {
          // categories: [
          //   "Jan",
          //   "Feb",
          //   "Mar",
          //   "Apr",
          //   "May",
          //   "Jun",
          //   "Jul",
          //   "Aug",
          //   "Sep",
          //   "Oct",
          //   "Nov",
          //   "Dec",
          //   "10 Jan",
          //   "11 Jan",
          //   "12 Jan",
          // ],
          categories: timeData,
        },
        yaxis: {
          labels: {
            style: {
              colors: "#3e4954",
              fontSize: "14px",
              fontFamily: "Poppins",
              fontWeight: 100,
            },
          },
        },
        fill: {
          colors: ["#f8857d", "#709fba"],
          type: "solid",
          opacity: 0.07,
        },
        grid: {
          borderColor: "#f1f1f1",
        },
      },
    };
  }

  render() {
    return (
      <div id="chart" className="line-chart-style bar-chart">
        <ReactApexChart
          options={this.state.options}
          series={this.state.series}
          type="area"
          height={350}
        />
      </div>
    );
  }
}

export default EntryExitPointChart;
